body {
  background-color: #1a1a1a;
}

.App {
  font-family: 'Roboto', sans-serif;
  text-align: center;
  padding: 20px;
  max-width: 600px;
  margin: 40px auto;
  background-color: #333;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  border-radius: 10px;
  color: #f0f0f0;
}

h1 {
  margin-bottom: 30px;
  color: #90caf9;
  font-weight: 500;
}

