.AddTodo {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.AddTodo input[type='text'] {
  flex-grow: 1;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
  background-color: #333;
  color: #f0f0f0;
}

.AddTodo input[type='submit'] {
  margin-left: 10px;
  padding: 5px 10px;
  background-color: #4caf50;
  color: #ffffff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.AddTodo input[type='submit']:hover {
  background-color: #43a047;
}

