.TodoItem {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  padding: 5px;
  background-color: #666;
  border-radius: 3px;
  color: #f0f0f0;
}

.TodoItem input[type='checkbox'] {
  margin-right: 10px;
}

.TodoItem span {
  flex-grow: 1;
  text-align: left;
}

.TodoItem span.completed {
  text-decoration: line-through;
  color: #999;
}

.TodoItem button {
  margin-left: 10px;
  padding: 5px;
  background-color: #f44336;
  color: #ffffff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.TodoItem button:hover {
  background-color: #d32f2f;
}

